import { Box, Breadcrumbs, Button, ButtonBase, Card, Dialog, DialogActions, DialogContent, DialogTitle, IconButton, List, ListItem, ListItemIcon, ListItemSecondaryAction, ListItemText, Typography } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "../../components/ajax";
import { useUserInfo } from "../../components/save";
import { useModalTheme } from "../../components/style";
import Defer from "../../components/ui/Defer";
import Navbar from "../../components/ui/Navbar";
import { expandStrapiImg, rupiah, winconfirm, winprompt } from "../../components/util";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import PinPrompt from "../../components/ui/market/PinPrompt";
import AddressInput from "../../components/ui/market/AddressInput";


const Prompt = ({ inquiry, open, onClose, onAction, address, setAddress, loading }) => {
    const modalTheme = useModalTheme();
    return (
        <Dialog
            open={open}
            onClose={onClose}
            fullWidth
        >
            <DialogTitle>
                Checkout
            </DialogTitle>
            <DialogContent>
                <Defer data={!loading}>
                    {() => <List>
                        <ListItem>
                            <ListItemText primary={'Alamat'} />
                            <ListItemSecondaryAction>
                                <AddressInput value={address} onChange={setAddress} />
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Harga Barang'} />
                            <ListItemText primary={rupiah(inquiry.data.cartPrice)} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Harga Antar'} />
                            <ListItemText primary={rupiah(inquiry.data.deliveryPrice)} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Harga Antar'} />
                            <ListItemText primary={rupiah(inquiry.data.cartPrice + inquiry.data.deliveryPrice)} className={modalTheme.right} primaryTypographyProps={{ variant: 'h6' }} />
                        </ListItem>
                    </List>}
                </Defer>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>Batal</Button>
                <Box flexGrow={1} />
                <Button color="primary" disabled={!address || loading} variant="contained" onClick={onAction}>Beli</Button>
            </DialogActions>
        </Dialog>
    );
}

const Page = () => {
    const user = useUserInfo();
    const [rel, setRel] = React.useState(1);
    const list = useFetcher(`/market/cart/?refresh=${rel}`);
    const modalTheme = useModalTheme();
    const [page, setPage] = React.useState(0);
    const [address, setAddress] = React.useState(0);
    const [store, setStore] = React.useState(null);
    const checkoutInfo = useFetcher(store ? `/market/cart/checkout?store_id=${store}&address_id=${address}&refresh=${rel}` : null);
    React.useEffect(() => {
        if (user && user.account.address) {
            setAddress(user.account.address);
        }
    }, [user])
    const edit = (id, qty) => {
        return () => {
            winprompt('Masukkan qty yang baru', qty).then(x => {
                x = parseInt(x);
                if (x && x != qty) {
                    setPage(1);
                    ajax.post('/market/cart/update', {
                        good_id: id,
                        qty: x,
                    }).then(() => {
                        alert('Berhasil disimpan');
                        setRel(rel + 1);
                    }).catch(x => {
                        alert(x.response?.data.message || x.message);
                    }).finally(() => {
                        setPage(0);
                    })
                }
            });
        }
    }
    const remove = (id) => {
        return () => {
            winconfirm('Hapus barang ini?').then(x => {
                if (x) {
                    setPage(1);
                    ajax.post('/market/cart/remove', {
                        good_ids: [id],
                    }).then(() => {
                        alert('Berhasil disimpan');
                        setRel(rel + 1);
                    }).catch(x => {
                        alert(x.response?.data.message || x.message);
                    }).finally(() => {
                        setPage(0);
                    })
                }
            });
        }
    }
    const confirm = (id) => {
        return () => {
            setStore(id);
            setPage(2);
        }
    }
    const checkout = (data) => {
        setPage(3);
    }
    const realCheckout = (pin) => {
        setPage(4);
        ajax.post('/market/cart/checkout', {
            store_id: store,
            pin,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setRel(rel + 1);
            setPage(0);
        });
    }
    return (
        <Defer data={user && list && page !== 1}>
            {() => <div>
                <Navbar />
                <List>
                    <ListItem>
                        <Breadcrumbs>
                            <Link color="inherit" to="/pasar/cari/">
                                Belanja
                            </Link>
                            <Typography color="textPrimary">Keranjang</Typography>
                        </Breadcrumbs>
                    </ListItem>
                    {
                        list.data.map(x => <ListItem key={x.id} className={modalTheme.expand}>
                            <Card className={modalTheme.expand}>
                                <List>
                                    <ListItem divider button onClick={() => navigate('/pasar/toko?id=' + x.id)}>
                                        <img src={expandStrapiImg(x.store.logo, 'thumbnail')} width="40px" style={{ marginRight: '10px' }} />
                                        <ListItemText primary={x.store.title} />
                                    </ListItem>
                                    {x.cart.map(y => <ListItem key={y.id} button onClick={() => navigate('/pasar/barang?id=' + y.good.id)}>
                                        <ListItemIcon>
                                            <img src={expandStrapiImg(y.good.galleries[0], 'thumbnail')} width="60px" style={{ marginRight: '10px' }} />
                                        </ListItemIcon>
                                        <ListItemText primary={y.good.title} secondary={`${rupiah(y.good.price)} x ${y.qty}`} />
                                        <ListItemSecondaryAction>
                                            <IconButton onClick={edit(y.good.id, y.qty)}>
                                                <EditIcon />
                                            </IconButton>
                                            <IconButton onClick={remove(y.good.id)}>
                                                <DeleteIcon />
                                            </IconButton>
                                        </ListItemSecondaryAction>
                                    </ListItem>)}
                                    <ListItem divider />
                                    <ListItem>
                                        <Button onClick={confirm(x.store.id)} variant="contained" color="primary">Checkout</Button>
                                    </ListItem>
                                </List>
                            </Card>
                        </ListItem>)
                    }
                    {
                        list.data.length === 0 && <ListItem>
                            <Box fontStyle="italic" width="100%" padding="20px">
                                <Typography align="center">
                                    Belum ada produk masuk ke keranjang
                                </Typography>
                            </Box>
                        </ListItem>
                    }
                </List>
                <Prompt open={page === 2} onClose={() => setPage(0)} onAction={checkout} inquiry={checkoutInfo} loading={!checkoutInfo} address={address} setAddress={setAddress} />
                <PinPrompt open={page >= 3} submitting={page === 4} onClose={() => setPage(2)} onAction={realCheckout} />
            </div>
            }
        </Defer>
    );
}

export default Page
